@import '../../../assets/style/vars.scss';

.footer {
  grid-area: footer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-grey);
  &_wrap {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid var(--color-brown);
    width: $container;
  }
  &_contacts {
    margin-top: 26px;
  }
  &_social {
    margin-top: 75px;
  }
  &_contacts_title {
    font-family: 'Montserrat Bold';
    font-size: 16px;
    color: var(--color-brown-hover);
    margin-bottom: 16px;
  }
  &_social_link {
    margin-left: 12px;
  }
  &_social_link_item {
    fill: var(--color-brown);
    transition: 0.4s ease-in-out;
  }
  &_social_link_item:hover {
    fill: var(--color-light-brown);
  }
  &_phone {
    margin-bottom: 16px;
  }
  &_phone a {
    font-family: 'TTRicordiMarmo';
    font-size: 12px;
    color: var(--color-brown);
    transition: 0.4s ease-in-out;
  }
  &_phone a:hover {
    color: var(--color-light-brown);
  }
  &_mail {
    margin-bottom: 27px;
  }
  &_mail a {
    font-family: 'TTRicordiMarmo';
    font-size: 12px;
    color: var(--color-brown);
    transition: 0.4s ease-in-out;
  }
  &_mail a:hover {
    color: var(--color-light-brown);
  }
}

.political {
  font-family: 'TTRicordiMarmo';
  font-size: 12px;
  align-self: flex-end;
  color: var(--color-brown);
  margin-bottom: 10px;
  transition: 0.4s ease-in-out;
  &:hover {
    color: var(--color-light-brown);
  }
}

// ==============MEDIA============

@media (max-width: 1340px) {
  .footer_wrap {
    width: 1150px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (max-width: 1170px) {
  .footer_wrap {
    width: 1000px;
  }
}

@media (max-width: 1022px) {
  .footer_wrap {
    width: 98%;
  }
}

@media (max-width: 692px) {
  .footer {
    &_contacts {
      margin-top: 35px;
    }
  }
}

@media (max-width: 404px) {
  .footer {
    &_contacts {
      margin-top: 48px;
    }
  }
}

@media (max-width: 700px) {
  .footer {
    &_contacts_title {
      font-size: 14px;
    }
    &_phone a {
      font-size: 10px;
    }
    &_phone {
      margin-bottom: 12px;
    }
    &_mail a {
      font-size: 10px;
    }
  }
  .political {
    font-size: 7px;
  }
}

@media (max-width: 404px) {
  .footer {
    &_contacts_title {
      font-size: 10px;
    }
    &_phone a {
      font-size: 9px;
    }
    &_phone {
      margin-bottom: 8px;
    }
    &_mail a {
      font-size: 9px;
    }
    &_social {
      margin-top: 65px;
    }
    &_social_link {
      & svg {
        width: 18px;
        height: 18px;
      }
    }
  }
  .political {
    position: absolute;
    left: 17px;
    font-size: 9px;
  }
}